import React, { Fragment } from "react";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes, Endpoint, Feature } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";

export default function EndpointModal() {
  const { licensed } = useAppStore()
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/endpoint", "create", {} as Endpoint);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Endpoint" />
      </ModalOpenButton>
      <ModalContent title="Create new endpoint" onClickOk={handleOk} formName="create_endpoint_form">
        <Form schemaName="endpoint">
          <FormContentBase
            name="create_endpoint_form"
            form={form}
            preserve={false}
            defaultValues={{
              url: "",
              method: "GET",
              authentication: licensed(Feature.Api)
            }}
          />
          {!licensed(Feature.Api) && <BuyLicenseAlertBox />}
        </Form>
      </ModalContent>
    </Fragment>
  );
}
